import React, { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';
import ThemeToggle from './ThemeToggle';

const Header = () => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const mobileMenuRef = useRef(null);

  // Close mobile menu when clicking outside (debounced)
  useEffect(() => {
    let timeoutId;
    const handleClickOutside = event => {
      clearTimeout(timeoutId);
      timeoutId = setTimeout(() => {
        if (
          mobileMenuRef.current &&
          !mobileMenuRef.current.contains(event.target) &&
          !event.target.closest('.mobile-menu-toggle')
        ) {
          setIsMobileMenuOpen(false);
        }
      }, 10);
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
      clearTimeout(timeoutId);
    };
  }, []);

  // Close mobile menu on escape key
  useEffect(() => {
    const handleEscape = event => {
      if (event.key === 'Escape') {
        setIsMobileMenuOpen(false);
      }
    };

    document.addEventListener('keydown', handleEscape);
    return () => {
      document.removeEventListener('keydown', handleEscape);
    };
  }, []);

  // Close mobile menu when navigating
  const handleLinkClick = () => {
    setIsMobileMenuOpen(false);
  };

  return (
    <header className='header'>
      <a href='#main-content' className='skip-to-content'>
        Skip to main content
      </a>
      <div className='container'>
        <div className='header-content'>
          <Link to='/' className='logo'>
            <span className='logo-text'>Sunny Dubey</span>
          </Link>

          {/* Desktop Navigation */}
          <nav className='nav desktop-nav'>
            <Link to='/blog'>blog</Link>
            <Link to='/projects'>projects</Link>
            <Link to='/about'>about</Link>
            <Link to='/uses'>uses</Link>
            <Link to='/now'>now</Link>
            <Link to='/changelog'>changelog</Link>
            <Link to='/resources'>resources</Link>
            <ThemeToggle />
          </nav>

          {/* Mobile Hamburger Button */}
          <button
            className='mobile-menu-toggle'
            onClick={() => {
              setIsMobileMenuOpen(!isMobileMenuOpen);
            }}
            aria-expanded={isMobileMenuOpen}
            aria-label='Toggle mobile menu'
            aria-controls='mobile-menu'
          >
            <span className='hamburger-line'></span>
            <span className='hamburger-line'></span>
            <span className='hamburger-line'></span>
          </button>
        </div>

        {/* Mobile Slide-out Menu */}
        {isMobileMenuOpen && (
          <div className='mobile-menu-overlay' onClick={handleLinkClick}>
            <nav
              className='mobile-menu'
              ref={mobileMenuRef}
              id='mobile-menu'
              role='navigation'
              aria-label='Mobile navigation'
            >
              <Link to='/blog' onClick={handleLinkClick}>
                blog
              </Link>
              <Link to='/projects' onClick={handleLinkClick}>
                projects
              </Link>
              <Link to='/about' onClick={handleLinkClick}>
                about
              </Link>
              <Link to='/uses' onClick={handleLinkClick}>
                uses
              </Link>
              <Link to='/now' onClick={handleLinkClick}>
                now
              </Link>
              <Link to='/changelog' onClick={handleLinkClick}>
                changelog
              </Link>
              <Link to='/resources' onClick={handleLinkClick}>
                resources
              </Link>
              <div className='mobile-menu-divider'></div>
              <a
                href='https://github.com/sunny-dubey'
                target='_blank'
                rel='noopener noreferrer'
                className='mobile-menu-link'
              >
                GitHub
              </a>
              <a
                href='https://www.linkedin.com/in/sunny-dubey-5018ab267/'
                target='_blank'
                rel='noopener noreferrer'
                className='mobile-menu-link'
              >
                LinkedIn
              </a>
              <a
                href='mailto:sunnydub2502@gmail.com'
                className='mobile-menu-link'
              >
                Email
              </a>
              <a
                href='/resume.pdf'
                target='_blank'
                rel='noopener noreferrer'
                className='mobile-menu-link'
              >
                Resume
              </a>
              <div className='mobile-theme-toggle'>
                <ThemeToggle />
              </div>
            </nav>
          </div>
        )}
      </div>
    </header>
  );
};

export default Header;
